import axios from 'axios'

const request = axios.create({
    baseURL:'/api',
    timeout:5000
})

request.interceptors.request.use((config)=>{
    return config;
})

request.interceptors.response.use(
    response => {
		return response
	},
	error => {
        error.message = 'asdasd'
		if(error.request){
			console.log(error.request)
		} else if(error.response){
			console.log(error.response.data);
			console.log(error.response.status);
		}
		if (error && error.response) {
			switch (error.response.status) {
				case 400: error.message = '(400)'; 
					break;
				case 401: error.message = '(401)';
					break;
				case 403: error.message = '(403)';
					break;
				case 404: error.message = '(404)'; 
					break;
				case 408: error.message = '(408)'; 
					break;
				case 500: error.message = '(500)'; 
					break;
				case 501: error.message = '(501)'; 
					break;
				case 502: error.message = '(502)'; 
					break;
				case 503: error.message = '(503)'; 
					break;
				case 504: error.message = '(504)'; 
					break;
				case 505: error.message = 'HTTP(505)';
					break;
				default: error.message = '';
			}
		}else{
			error.message ='!'
		}
		return Promise.reject(error)
	}
)

export default request